$address-box-border-color: $FrenchGray;
$address-box-add-color: $Niagara;
$address-box-selected-label-bg-color: $Niagara;
$address-box-selected-label-color: $White;
$address-box-selected-delete-btn-color: $White;
$address-box-selected-edit-btn-color: $White;

[data-delivery] {
	padding-bottom: 56px;

 .o-simpleSubTotal {
		position: fixed;
		z-index: 11;
		bottom: 0;
		width: 100%;
	}
}

.o-address {
	$address: &;
	padding: $md;

	&__box {
    display: flex;
    align-items: center;
    margin-bottom: $md;
    border: 1px dashed $address-box-border-color;
    @include border-radius(5px);
		font-size: $fs-14;

		&::before {
			margin-right: $md;
			font-size: $fs-18;
		}

		&.-addAddress {
			padding: $md;
			color: $address-box-add-color;
			cursor: pointer;
		}

		&.-selectShop {
			padding: $md;
			cursor: pointer;
		}

		&.-shop {
			cursor: pointer;

			#{$address}__label {
				padding: $md;
			}

			#{$address}__select::after {
				min-width: auto;
			}

			#{$address}__radio:checked ~ #{$address}__label {
				padding: $sm;

				#{$address}__select::before {
					margin-bottom: 0;
					font-size: $fs-26;
				}

				#{$address}__select::after {
					content: "";
				}
			}
		}
	}

	&__radio {
		display: none;
	}

	&__radio:checked ~ &__label {
		background-color: $address-box-selected-label-bg-color;
		color: $address-box-selected-label-color;

		#{$address}__select::before {
			display: flex;
			margin-bottom: $xs;
			font-size: $fs-30;
		}

		#{$address}__select::after {
			content: "Seçili";
		}

		#{$address}__deleteEdit {
			display: flex;
		}

		#{$address}__delete {
			color: $address-box-selected-delete-btn-color;
		}

		#{$address}__edit {
			color: $address-box-selected-edit-btn-color;
		}
	}

	&__label {
		display: flex;
		align-items: center;
		width: 100%;
		padding: $sm;
		cursor: pointer;
	}

	&__select {
		display: flex;
		flex-direction: column;
		align-items: center;
		padding-right: $md;
		font-size: $fs-11;
		font-weight: $bold;
		text-align: center;
		text-transform: uppercase;
	}

	&__select::before {
		display: none;
	}

	&__select::after {
		content: "Seç";
		min-width: 35px;
	}

	&__detail {
		display: flex;
		flex-direction: column;
	}

	&__info {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: $sm;
    line-height: 1.5rem;
	}

	&__deleteEdit{
	 a {
			margin-right: $sm;
			font-weight: $bold;
		}
	}

	&__name {
		margin-right: $md;
    font-weight: $bold;
	}

	&__phone {
		margin-right: $md;
	}

	&__deleteEdit {
		display: none;
	}

	&__sameAddress {
		font-size: $fs-14;
		margin-bottom: $md;
	}
}