$step-item-bg-color: $Porcelain;
$step-item-color: $Gray;
$step-item-active-color: $Niagara;

.o-step {
  display: flex;

  &__item {
		flex-basis: 50%;
		padding: $md 0;
		background-color: $step-item-bg-color;
		color: $step-item-color;
		text-align: center;
		
    &.-active {
			border-bottom: 2px solid;
			color: $step-item-active-color;
    }
	}
	
	&__title {
		font-size: $fs-16;
	}
}