$modal-content-bg-color: $White;

.o-modal {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;

  display: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;

  $modal: &;
  transition: all .3s ease;

  &.-open {
    display: flex;

    #{$modal}__content {
      opacity: 1;
    }
  }

  &__content {
    z-index: z("penthouse");

    width: 100%;
    height: 100%;

    background-color: $modal-content-bg-color;
  }

  &__header {
    width: 100%;
  }

  &__body {
    overflow: auto;
    height: calc(100vh - 106px);

    padding: $md;
  }

  &__title {
    margin: $md 0;

    font-size: $fs-20;
  }

  &__description {
    margin: $md 0;

    font-size: $fs-14;
  }
}
